.ximg-demo {
  width: 100%;
  height: auto;
  display: block;
}
#video {
  position: fixed;
  top: 100%;
  z-index: 20;
  overflow: hidden;
  width: 100%;
  height: 100%;
  background-color: #fff;
  -webkit-transform: scale(0, 0);
  -ms-transform: scale(0, 0);
  transform: scale(0, 0);
}
.animate #video {
  -webkit-transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
}
#video.show {
  top: 0;
  -webkit-transform: scale(1, 1);
  -ms-transform: scale(1, 1);
  transform: scale(1, 1);
}
#video video {
  position: absolute;
  top: 30%;
  display: block;
  width: 100%;
}
@keyframes rotating {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
.dazhuanpan {
  animation: rotating 10s linear infinite;
}
@keyframes scale {
  from {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  to {
    transform: scale(1);
  }
}
.dazhuanpan-go {
  animation: scale 2s linear infinite;
}



